.hover-card {
  transition: all .30s ease;
}

.hover-card:hover {
  -webkit-transform: translateY(-4px) scale(1.01);
  transform: translateY(-4px) scale(1.01);
  box-shadow: 0 15px 25px rgba(60, 50, 100, 0.1);
}

/* stats card */
.stats-card {
  background-color: #fff;
  padding: 20px;
  position: relative;
  border-radius: 4px;
  box-shadow: 0 1px 15px rgba(0, 0, 0, 0.04), 0 1px 6px rgba(0, 0, 0, 0.04);
}

.stats-card h3 {
  margin: 0;
  font-size: 22px;
}

.stats-card h3 .icon {
  margin-left: 5px;
  width: 18px;
  height: 18px;
  margin-top: -4px;
}

.stats-card p {
  margin: 5px 0 0;
  line-height: 1;
}

.stats-card i {
  position: absolute;
  right: 0;
  top: 0;
  color: #fff;
  height: 50px;
  width: 50px;
  border-radius: 30px 0 30px 30px;
  transition: .5s;
  text-align: center;
  line-height: 50px;
  font-size: 25px;
}

.stats-card:hover i {
  height: 100%;
  border-radius: 0 4px 4px 0;
  width: 70px;
  line-height: 85px;
  font-size: 35px;
}

.stats-card:hover .icon-hover {
  height: 60px;
  width: 60px;
  line-height: 60px;
  border-radius: 30px 0 30px 30px;
  font-size: 30px;
}

.stats-card .stats-icon {
  position: absolute;
  right: 0;
  top: 0;
  color: #fff;
  height: 50px;
  width: 50px;
  padding: 10px;
  border-radius: 30px 0 30px 30px;
  transition: .5s;
}

.stats-card:hover .stats-icon {
  height: 100%;
  border-radius: 0 4px 4px 0;
  width: 70px;
  padding: 15px;
}

